@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.contacts-page {
  padding-bottom: 176px;
  color: var.$gray;
  font-family: var.$font-family;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 300;
  line-height: 1.66;

  @include mixin.laptop {
    padding-bottom: 104px;
  }

  @include mixin.small-laptop {
    padding-bottom: 84px;
  }

  @include mixin.wide-tablet {
    padding-bottom: 64px;
  }

  &__head {
    display: flex;
    justify-content: space-between;
    margin-bottom: 32px;

    .title-2 {
      margin-bottom: 0 !important;
    }
  }

  &__block {
    margin-bottom: 96px;

    @include mixin.small-tablet {
      margin-bottom: 64px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    .title-2 {
      margin-bottom: 48px;

      @include mixin.small-tablet {
        margin-bottom: 32px;
      }
    }

    .person-row {
      gap: 30px 0;
      margin-left: -15px;
      margin-right: -15px;

      &>div {
        padding: 0 15px;
      }
    }
  }

  .contact-socials {
    @include mixin.small-tablet {
      margin-top: 16px;
    }
  }

  .contacts-title-6 {
    margin-bottom: 8px;
  }
}

.contacts {
  display: flex;
  flex-wrap: wrap;
  gap: 32px;

  @include mixin.small-tablet {
    gap: 16px;
  }
}

.contact {
  min-width: 45%;

  &__socials {
    display: flex;
    gap: 24px;

    a {
      path {
        transition: 0.3s linear;
      }

      &:hover {
        path {
          fill: var.$primary;
        }
      }
    }

    li:last-child {
      a:hover {
        path {
          fill: white;

          &:first-child {
            fill: var.$primary;
          }
        }
      }
    }
  }
}

.sub-heads {
  max-width: 270px;

  a {
    color: inherit;
  }
}

.person {
  margin-bottom: 48px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 48px;

  @include mixin.tablet {
    gap: 16px;
    margin-bottom: 32px;
  }

  &:nth-child(2) {
    @include mixin.tablet {
      margin-bottom: 16px;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__ava {
    flex-shrink: 0;
    overflow: hidden;
    width: 150px;
    height: 150px;

    @include mixin.tablet {
      width: 72px;
      height: 72px;
      border-bottom-right-radius: 20px;
      border-top-left-radius: 20px;
    }

    img {
      width: 100%;
    }
  }

  &__data {
    flex-grow: 1;
  }

  &__head {
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 32px;

    @include mixin.tablet {
      margin-bottom: 16px;
    }
  }

  &__connection {
    padding: 12px 16px;
    box-sizing: border-box;
    width: 100%;
    max-width: 414px;
    color: var.$secondary;
    text-align: center;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.5px;

    @include mixin.tablet {
      display: none;
    }
  }

  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-style: normal;
    font-weight: 700;
    line-height: 1.66;
    margin-bottom: 8px;

    @include mixin.tablet {
      font-size: 1.8rem;
    }
  }

  &__role {
    color: var.$gray;
    font-family: var.$font-family;
    font-size: 2.2rem;
    font-style: normal;
    font-weight: 300;
    line-height: 1.45;
    letter-spacing: 0.3px;

    @include mixin.wide-tablet {
      font-size: 1.8rem;
    }
  }

  &__contacts {
    width: 100%;
    display: flex;
    align-items: flex-start;
    gap: 22px;
    font-size: 1.6rem;

    @include mixin.tablet {
      flex-wrap: wrap;
      gap: 16px;
    }
  }

  &__contact {
    flex-basis: 270px;
  }

  &__connection-mob {
    display: none;
    padding: 12px 16px;
    box-sizing: border-box;
    width: 100%;
    max-width: 414px;
    color: var.$secondary;
    text-align: center;
    font-family: var.$font-family;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.5px;
    margin-bottom: 32px;

    @include mixin.tablet {
      display: block;
    }
  }
}

.dirs {
  .row {
    gap: 32px 0;
  }
}

.requests {
  &__tags {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 48px;
    overflow-y: auto;
    scrollbar-width: none;

    @include mixin.small-tablet {
      margin-bottom: 32px;
    }

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__tag {
    padding: 12px 24px;
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 1.5;
    text-align: left;

    &:hover {
      background: var.$primary;
      color: var.$light;
    }

    @include mixin.small-tablet {
      padding: 8px 24px;
    }

    &.badge--orange {
      color: var.$light;
    }
  }
}

.request-item {
  padding-bottom: 48px;
  margin-bottom: 48px;
  border-bottom: 1px solid #E4E4E4;

  @include mixin.wide-tablet {
    padding-bottom: 24px;
    margin-bottom: 32px;
  }

  &--active {
    @include mixin.wide-tablet {
      padding-bottom: 7px;
      border-color: transparent;
    }
  }

  &:last-child {
    padding-bottom: 0;
    border: none;
  }

  &__head {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;

    @include mixin.small-tablet {
      gap: 8px;
    }
  }

  &__title {
    color: var.$secondary;
    font-family: var.$font-family;
    font-size: 2.4rem;
    font-weight: 700;
    line-height: 1.66;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;

    @include mixin.small-tablet {
      font-size: 2rem;
      font-weight: 500;
      line-height: 1.6;
    }

    a {
      margin-top: 16px;
      color: var.$gray;
      font-family: var.$font-family;
      font-size: 2.2rem;
      font-weight: 300;
      line-height: 1.45;
      letter-spacing: 0.3px;

      @include mixin.small-tablet {
        margin-top: 10px;
        font-size: 1.8rem;
        font-weight: 300;
      }

      &:hover {
        color: var.$gray;
      }
    }
  }

  &--links:hover &__title,
  &--links.procurement-item--active &__title {
    color: var.$primary;
    text-decoration-line: underline;
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 100px;
    background: rgba(var.$secondary, 0.10);
    transition: 0.3s linear;
    transform-origin: center;

    svg {
      width: 24px;
      height: 24px;

      path {
        stroke: var.$secondary;
      }
    }
  }

  &--active &__btn {
    background: rgba(var.$primary, 0.10);
    transform: rotateZ(180deg);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &--links &__btn {
    background: rgba(var.$primary, 0.10);

    svg {
      path {
        stroke: var.$primary;
      }
    }
  }

  &--active.procurement-item--active &__btn {
    transform: rotateZ(0deg);
  }

  &__body {
    margin-top: 0;
    max-height: 0;
    display: flex;
    gap: 32px;
    flex-wrap: wrap;
    transition: 0.3s linear;
    overflow: hidden;

    @include mixin.small-tablet {
      gap: 16px;
    }
  }

  &--active &__body {
    max-height: 600px;
    margin-top: 32px;

    @include mixin.wide-tablet {
      margin-top: 24px;
    }

    @include mixin.small-tablet {
      margin-top: 16px;
    }
  }

  &.transitionend &__body {
    overflow-y: auto;
  }

  &__contact {
    a {
      color: var.$gray;

      &:hover {
        color: var.$gray;
      }
    }
  }
}

.map {
  width: 100%;
  aspect-ratio: 1.5;
  border-top-left-radius: 80px;
  border-bottom-right-radius: 80px;
  overflow: hidden;
  border: 5px solid #E4E4E4;
}